import React from 'react'
import ml5 from 'ml5'

const defaultImage = "data:image/jpeg;base64,";

export default class ImageClassifier extends React.Component {

    constructor() {
        super();
        this.clf = ml5.imageClassifier('MobileNet', () => console.log("model ready"));

        this.state = {
            image: defaultImage,
        };

        this.classify = this.classify.bind(this);
        this.onImageChange = this.onImageChange.bind(this);
    }

    classify() {
        this.clf.predict(document.getElementById("image"), (err, results) => {
            if(err) {
                console.log(err)
            } else {
                this.setState({prediction: results[0].className, confidence: results[0].probability.toFixed(4)})

            }
        })
    }

    onImageChange(e) {
        let image = defaultImage;
        if(e.target.value !== "") {
            image = e.target.value;
        }
        this.setState({image, prediction: null, confidence: null})
    }

    render() {
        return (<div>
            <h2>Predict the image by providing a url.</h2>
            <img src={this.state.image} alt={"puffin"} id={"image"} width={200} height={150}/>
            <br/>
            <button onClick={this.classify}>Predict</button>
            {this.state.prediction && this.state.confidence && <h4>The above image is predicted as {this.state.prediction} with {(this.state.confidence * 100).toFixed(2)} percentage.</h4>}
            <h3>Enter the url of the image you want to predict.</h3>
            <textarea onChange={this.onImageChange} rows={5} cols={40}/>
        </div>)
    }

}


