import React from 'react'
import Camera from 'react-camera'
import ml5 from 'ml5'

export default class EmotionPredictor extends React.Component {

    constructor() {
        super();

        this.clf = ml5.featureExtractor('MobileNet', () => console.log("emotion predictor model is ready!!"));

        this.state = {
            emotion: "Train first..",
            happyImages: 0,
            sadImages: 0
        };

        this.addEmotion = this.addEmotion.bind(this);
        this.train = this.train.bind(this);
        this.gotResults = this.gotResults.bind(this);
    }

    componentDidMount() {
        this.classifier = this.clf.classification(this.camera.video, () => console.log("camera is ready.."))
    }

    addEmotion(emotion) {
        this.classifier.addImage(emotion);
        if(emotion === "happy") {
            this.setState({happyImages: this.state.happyImages+1})
        } else {
            this.setState({sadImages: this.state.sadImages+1})
        }
    }

    train() {
        this.classifier.train((loss) => {
            if(loss === null) {
                console.log("Trianing Complete");
                this.classifier.classify(this.gotResults);
            } else {
                console.log(loss)
            }
        })
    }

    gotResults(err, result) {
        if(err) {
            console.log(err)
        } else {
            this.setState({emotion: result});
            this.classifier.classify(this.gotResults)
        }
    }


    render() {

        return (<div id={"video-container"}>
            <h4>Predict your emotions..</h4>
            <Camera ref={cam => this.camera=cam} video={true} style={{width: 1000, height: 600, margin: "auto", position: "relative", background: "#000000"}}/>
            <h4>Current emotion: {this.state.emotion}</h4>
            <button onClick={() => this.addEmotion("happy")}>Add happy face</button> {' '}
            <button onClick={() => this.addEmotion("sad")}>Add sad face</button>{' '}
            <button onClick={this.train}>Train</button><br/>
            <h4>Happy Images: {this.state.happyImages} Sad Images: {this.state.sadImages}</h4>
        </div>)

    }

}


