import React from 'react'
import Camera from 'react-camera'
import ml5 from 'ml5'

export default class VideoCapturing extends React.Component {

    constructor() {
        super();

        this.clf = ml5.featureExtractor('MobileNet', () => console.log("model is ready!!"));

        this.state = {
            sliderValue: 0.5,
            imageAdded: 0,
        };

        this.onSliderValue = this.onSliderValue.bind(this);
        this.addPosition = this.addPosition.bind(this);
        this.train = this.train.bind(this);
        this.gotResults = this.gotResults.bind(this);
    }

    componentDidMount() {
        this.regressor = this.clf.regression(this.camera.video, () => console.log("camera ready!!"));
    }

    onSliderValue(e) {
        this.setState({sliderValue: e.target.value})
    }

    addPosition() {
        this.regressor.addImage(this.state.sliderValue);
        this.setState({imageAdded: this.state.imageAdded+1})
    }

    train() {
        this.regressor.train((loss) => {
            if(loss === null) {
                console.log("Training complete");
                this.regressor.predict(this.gotResults);
            } else {
                console.log(loss)
            }
        })
    }

    gotResults(err, result) {
        if(err) {
            console.log(err);
        } else {
            this.setState({sliderValue: result});
            this.regressor.predict(this.gotResults);
        }
    }

    render() {
        return (<div id={"video-container"}>
            <h2>Train the slider to follow ur hand.</h2>
            <Camera ref={cam => this.camera=cam} video={true} style={{width: 1000, height: 600, margin: "auto", position: "relative", background: "#000000"}}>
                <div style={{width: 50, height: 50, background: "red", position: "absolute", top: 300, left: 200 + this.state.sliderValue*600}}/>
            </Camera>
            <input type={"range"} min={0} max={1} step={0.01} value={this.state.sliderValue} onChange={this.onSliderValue}/>
            <br/>
            <button onClick={this.addPosition}>Add</button>{' '}
            <button onClick={this.train}>Train</button>
            <h5>Image added: {this.state.imageAdded}</h5>
        </div>)
    }

}


